/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
  transition: all 0.2s linear;
}

html {
  height: 100%;
  font-size: 62.5%;
  overflow-x: hidden;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: transparent;
}
html::-webkit-scrollbar-thumb {
  background: #000;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #f4f4f4;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

header {
  flex: 1 0 auto;
}

main {
  flex: 1 0 auto;
}

footer {
  flex: 0 0 auto;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  padding: 6rem 1.5rem;
}
@media (max-width: 1200px) {
  .container {
    max-width: 83rem;
  }
}
@media (max-width: 829px) {
  .container {
    max-width: 60rem;
  }
}

.row {
  display: flex;
  justify-content: space-between;
}

.main {
  flex: 1 1 87rem;
}

.sidebar {
  flex: 1 1 27rem;
  min-width: 20rem;
}
@media (max-width: 829px) {
  .sidebar {
    margin-left: 0 !important;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: #fff;
    padding: 1.5rem;
    box-shadow: 0 0 2rem 0.4rem rgba(0, 0, 0, 0.37);
    overflow-y: auto;
    transform: translateX(-150%);
    transition: transform 0.3s ease-in;
  }
}

.sidebar--mobile-active {
  transform: translateX(0%);
}

.main + .sidebar {
  margin-left: 3rem;
}

@font-face {
  font-family: "Poppins-Black";
  src: url("../fonts/Poppins-Black.woff2") format("woff2"), url("../Poppins-Black.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.cards-holder {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 599px) {
  .cards-holder {
    flex-direction: column;
  }
}

.cards-holder > .card-link {
  margin-bottom: 3rem;
}

.card-link {
  display: inline-block;
  text-decoration: none;
}
.card-link:hover {
  opacity: 0.8;
  transform: translateY(-0.5rem);
}

.card {
  position: relative;
  width: 27rem;
  background-color: #fff;
}
.card__badges {
  position: absolute;
  top: 1rem;
  left: 1.5rem;
  display: flex;
}
.card__badges > * + * {
  margin-left: 1rem;
}
.card__badge {
  display: inline-block;
  border-radius: 0.4rem;
  height: 2.2rem;
  padding: 0 1rem;
  font-size: 1.4em;
  line-height: 2.3rem;
  color: #000;
}
.card__badge--class {
  background-color: #FFCF26;
}
.card__badge--credit {
  background-color: #11BE75;
}
.card__content {
  padding: 1.6rem 1.5rem 2rem;
  width: 27rem;
}
.card__title {
  font-size: 1.8rem;
  line-height: 1.3;
  font-weight: 700;
  color: #000;
  margin-bottom: 1.1rem;
}
.card__term, .card__metro, .card__adress {
  font-size: 1.4rem;
  line-height: 1.142;
  color: #000;
}
.card__term, .card__metro {
  margin-bottom: 1.1rem;
}
.card__metro {
  position: relative;
}
.card__metro::before {
  content: "";
  display: inline-block;
  margin-right: 1rem;
  vertical-align: baseline;
  width: 1.6rem;
  height: 1.1rem;
  background-image: url("./../img/svg/metro.svg");
  background-repeat: no-repeat;
  background-size: initial;
}
.card__adress {
  color: #e7e7e7;
}

.card-link--hidden {
  display: none;
}

.widget__title {
  position: relative;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.16;
  color: #000;
  margin-bottom: 2.1rem;
  cursor: pointer;
}
.widget__title::before {
  content: "";
  position: absolute;
  top: -0.1rem;
  right: 0.1rem;
  width: 2rem;
  height: 2rem;
  background-image: url("./../img/svg/chevron-up.svg");
  background-repeat: no-repeat;
  background-size: initial;
  background-position: center;
}
.widget__body--hidden {
  display: none;
}
.widget__title--active::before {
  transform: rotate(180deg);
}
.widget__hidden {
  display: none;
}
.widget__show-hidden {
  display: inline-block;
  border: none;
  background: transparent;
  font-family: inherit;
  font-size: 1.6em;
  line-height: 1.19;
  color: #4290C9;
  cursor: pointer;
  margin-top: 1.5rem;
  padding: 0;
}
.widget__show-hidden:hover {
  color: #007fda;
  text-decoration: underline;
}
.widget__show-hidden::placeholder {
  font-size: 1.6em;
  line-height: 1.19;
  font-family: inherit;
  color: #4290C9;
}
.widget__show-hidden:active, .widget__show-hidden:focus {
  outline: none;
}

.widget + .widget {
  margin-top: 3.3rem;
}

.location__row {
  display: flex;
}
.location__row:first-child {
  overflow: hidden;
  border-radius: 0.4rem 0.4rem 0 0;
  border: 0.1rem solid #d6d6d6;
}
.location__row:last-child {
  overflow: hidden;
  border-radius: 0 0 0.4rem 0.4rem;
  border: 0.1rem solid #d6d6d6;
  border-top: none;
}
.location__checkbox {
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  position: absolute;
  appearance: none;
}
.location__checkbox:checked + .location__btn {
  background-color: #FFCF26;
}
.location__checkbox:checked + .location__btn:hover {
  background-color: #e0b600;
}
.location__btn {
  flex-grow: 1;
  font-family: inherit;
  border: none;
  background-color: transparent;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 5rem;
  color: #3c3c3c;
  text-align: center;
  padding: 0 1.5rem;
  height: 4.8rem;
  cursor: pointer;
}
.location__btn:hover {
  background-color: #fff0bb;
}

.radio {
  display: block;
  cursor: pointer;
}

.radio + .radio {
  margin-top: 1.5rem;
}

.radio-real {
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  position: absolute;
  appearance: none;
}

.radio-real:checked + .radio-fake {
  background-color: #ffe074;
}

.radio-real:checked + .radio-fake::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 0.8rem;
  height: 0.8rem;
  background-color: #3c3c3c;
  border-radius: 50%;
}

.radio-real:checked ~ .radio-title {
  font-weight: 500;
}

.radio-fake {
  position: relative;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-color: #fff;
  border: 0.1rem solid #777777;
  border-radius: 50%;
  margin-right: 0.7rem;
  vertical-align: bottom;
}

.radio-title {
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.187rem;
  color: #000;
}

.checkbox {
  display: block;
  cursor: pointer;
}

.checkbox--hidden {
  display: none;
}

.checkbox + .checkbox {
  margin-top: 1.5rem;
}

.checkbox-real {
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  position: absolute;
  appearance: none;
}

.checkbox-real:checked ~ .checkbox-title {
  font-weight: 500;
}

.checkbox-fake {
  position: relative;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background-color: transparent;
  border: 0.1rem solid #777777;
  border-radius: 0.2rem;
  margin-right: 0.7rem;
  vertical-align: bottom;
}

.checkbox-real:checked + .checkbox-fake {
  background-color: #ffe074;
}

.checkbox-real:checked + .checkbox-fake::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("./../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: initial;
  background-position: center;
  width: 1.8rem;
  height: 1.8rem;
}

.checkbox-title {
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.187rem;
  color: #000;
}

.toggle {
  cursor: pointer;
}
.toggle__real {
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  position: absolute;
  appearance: none;
}
.toggle__real:checked + .toggle__fake {
  background-color: #ffe074;
}
.toggle__real:checked + .toggle__fake::before {
  left: 1.9rem;
}
.toggle__fake {
  position: relative;
  display: inline-block;
  width: 3.7rem;
  height: 2rem;
  background-color: #c4c4c4;
  border-radius: 1rem;
  vertical-align: bottom;
  margin-right: 0.7rem;
}
.toggle__fake::before {
  position: absolute;
  content: "";
  top: 0.2rem;
  left: 0.2rem;
  width: 1.6rem;
  height: 1.6rem;
  background-color: #3c3c3c;
  border-radius: 50%;
}
.toggle__title {
  font-family: inherit;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.187;
  color: #000;
}
.toggle .toggle-real:checked ~ .toggle__title {
  font-weight: 500;
}

.widget--top-30 {
  margin-top: 3rem !important;
}

.btn-more {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 8rem;
  background-color: #e5e5e5;
  border-radius: 0.4rem;
  border: none;
  text-align: center;
  font-size: 1.8rem;
  line-height: 1.16;
  color: #4290C9;
  cursor: pointer;
}
.btn-more:hover {
  background-color: #d8d8d8;
}
.btn-more img {
  margin-right: 1rem;
}

.form-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  color: #3c3c3c;
  font-size: 1.6rem;
  line-height: 1;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  height: 5rem;
  background-color: transparent;
  text-align: center;
  border: 0.1rem solid #d6d6d6;
  border-radius: 0.4rem;
  cursor: pointer;
}
.form-btn:hover {
  background-color: #d8d8d8;
}

.form-btn + .form-btn {
  margin-top: 1rem;
}

.form-btn--submit {
  background-color: #FFCF26;
  border: none;
  color: #000;
}
.form-btn--submit:hover {
  background-color: #eec430;
}

.toggle-sidebar {
  display: none;
  position: fixed;
  top: 0.8rem;
  right: 1.5rem;
  width: 4.5rem;
  height: 4.5rem;
  border: none;
  cursor: pointer;
  z-index: 1000;
}
@media (max-width: 829px) {
  .toggle-sidebar {
    display: block;
  }
}

.menu-icon-wrapper {
  width: 4.5rem;
  height: 4.5rem;
  display: flex;
  align-items: center;
  background-color: rgb(71, 71, 71);
}

.menu-icon {
  position: relative;
  width: 3rem;
  height: 0.5rem;
  background-color: #fff;
}
.menu-icon::before {
  content: "";
  position: absolute;
  left: 0;
  top: -1rem;
  width: 3rem;
  height: 0.5rem;
  background-color: #fff;
}
.menu-icon::after {
  content: "";
  position: absolute;
  left: 0;
  top: 1rem;
  width: 3rem;
  height: 0.5rem;
  background-color: #fff;
}

.menu-icon.menu-icon-active {
  background-color: transparent;
}

.menu-icon.menu-icon-active::after {
  transform: rotate(-45deg);
  top: 0;
}

.menu-icon.menu-icon-active::before {
  transform: rotate(45deg);
  top: 0;
}